.portal-root {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(180deg, #f5f5f5 0%, #ffffff 100%);
}

.portal-card {
  width: 360px;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 8px 20px rgba(0,0,0,0.12);
  background: #fff;
  text-align: center;
}

.portal-title {
  margin: 0 0 12px 0;
  font-weight: 700;
  font-size: 18px;
}

.portal-body {
  padding: 12px 0 6px 0;
}

.portal-button {
  background: #c5050c;
  color: white;
  border: none;
  padding: 12px 18px;
  border-radius: 6px;
  font-weight: 700;
  cursor: pointer;
  width: 100%;
}

.portal-button:hover {
  background: #a00308;
}
